import { NextRequest, NextResponse } from 'next/server';
import db from '@/lib/db';
import { generateUniqueSlug } from '@/lib/slug';
export async function GET(_request: NextRequest, context: {
  params: Promise<{
    id: string;
  }>;
}) {
  try {
    // Wait for params to be resolved in Next.js 13+ App Router
    const params = await context.params;
    const productIdOrSlug = params.id;
    if (productIdOrSlug === undefined) {
      console.error('Parameter is undefined');
      return NextResponse.json({
        success: false,
        error: 'Product parameter is missing'
      }, {
        status: 400
      });
    }

    // Prefer slug lookup for better SEO and UX
    // First try to find by slug (non-numeric string)
    const isNumericId = /^\d+$/.test(productIdOrSlug);
    let productResult;
    if (!isNumericId) {
      // Parameter is a slug (string with non-numeric characters)
      productResult = await db.query('SELECT * FROM products WHERE slug = $1', [productIdOrSlug]);
    } else {
      // Parameter is a numeric ID
      const productId = parseInt(productIdOrSlug, 10);
      if (isNaN(productId)) {
        return NextResponse.json({
          success: false,
          error: `Invalid product ID: ${productIdOrSlug}`
        }, {
          status: 400
        });
      }
      productResult = await db.query('SELECT * FROM products WHERE id = $1', [productId]);
    }
    if (productResult.rows.length === 0) {
      return NextResponse.json({
        success: false,
        error: 'Product not found'
      }, {
        status: 404
      });
    }
    if (productResult.rows.length === 0) {
      return NextResponse.json({
        success: false,
        error: 'Product not found'
      }, {
        status: 404
      });
    }
    const product = productResult.rows[0];

    // Fetch associated images for the product
    // Handle case where product_images table might not exist yet
    let productWithImages;
    try {
      const imagesResult = await db.query('SELECT image_url, is_primary, sort_order FROM product_images WHERE product_id = $1 ORDER BY sort_order', [product.id]);
      productWithImages = {
        ...product,
        images: imagesResult.rows.map((img: any) => img.image_url),
        primary_image: imagesResult.rows.find((img: any) => img.is_primary)?.image_url || product.image_url
      };
    } catch (imageError) {
      // If product_images table doesn't exist, return product with just the original image_url
      console.warn(`Could not fetch images for product ${product.id}:`, (imageError as Error).message || imageError);
      productWithImages = {
        ...product,
        images: product.image_url ? [product.image_url] : [],
        primary_image: product.image_url
      };
    }
    return NextResponse.json({
      success: true,
      data: productWithImages
    });
  } catch (error) {
    console.error('Error fetching product:', error);
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, {
      status: 500
    });
  }
}
export async function PUT(request: NextRequest, {
  params
}: {
  params: {
    id: string;
  };
}) {
  try {
    const {
      id
    } = params;

    // Convert the id to integer for database query
    const productId = parseInt(id, 10);
    const body = await request.json();
    const {
      name,
      description,
      price,
      image_url,
      image_urls,
      category
    } = body;
    if (!name || !description || !price || price <= 0) {
      return NextResponse.json({
        success: false,
        error: 'Name, description, and price are required'
      }, {
        status: 400
      });
    }

    // First fetch the existing product
    const existingProductResult = await db.query('SELECT * FROM products WHERE id = $1', [productId]);
    if (existingProductResult.rows.length === 0) {
      return NextResponse.json({
        success: false,
        error: 'Product not found'
      }, {
        status: 404
      });
    }
    const existingProduct = existingProductResult.rows[0];

    // If name has changed, generate a new unique slug
    let updatedFields = 'name = $1, description = $2, price = $3, image_url = $4, category = $5';
    const queryParams = [name, description, price, image_url, category];
    if (name !== existingProduct.name) {
      // Generate new slug based on the new name
      const newSlug = await generateUniqueSlug(name, productId);
      updatedFields += ', slug = $6';
      queryParams.push(newSlug);
      queryParams.push(productId); // Id for WHERE clause
    } else {
      queryParams.push(productId); // Id for WHERE clause
    }

    // Update the product
    const productResult = await db.query(`UPDATE products SET ${updatedFields} WHERE id = $${queryParams.length} RETURNING *`, queryParams);
    if (productResult.rows.length === 0) {
      return NextResponse.json({
        success: false,
        error: 'Product not found'
      }, {
        status: 404
      });
    }
    const product = productResult.rows[0];

    // Delete existing images for this product (handle case where table might not exist)
    try {
      await db.query('DELETE FROM product_images WHERE product_id = $1', [productId]);
    } catch (deleteError) {
      console.warn(`Could not delete images for product ${productId}:`, (deleteError as Error).message || deleteError);
    }

    // If multiple images are provided, add them to the product_images table
    if (Array.isArray(image_urls) && image_urls.length > 0) {
      try {
        for (let i = 0; i < image_urls.length; i++) {
          const isPrimary = i === 0; // Make the first image primary by default
          await db.query('INSERT INTO product_images (product_id, image_url, is_primary, sort_order) VALUES ($1, $2, $3, $4)', [product.id, image_urls[i], isPrimary, i]);
        }
      } catch (insertError) {
        console.warn(`Could not insert images for product ${product.id}:`, (insertError as Error).message || insertError);
      }
    } else if (image_url) {
      // If only a single image is provided, add it as a primary image
      try {
        await db.query('INSERT INTO product_images (product_id, image_url, is_primary, sort_order) VALUES ($1, $2, $3, $4)', [product.id, image_url, true, 0]);
      } catch (insertError) {
        console.warn(`Could not insert image for product ${product.id}:`, (insertError as Error).message || insertError);
      }
    }

    // Fetch the updated product with its images to return
    // Handle case where product_images table might not exist yet
    let productWithImages;
    try {
      const imagesResult = await db.query('SELECT image_url, is_primary, sort_order FROM product_images WHERE product_id = $1 ORDER BY sort_order', [product.id]);
      productWithImages = {
        ...product,
        images: imagesResult.rows.map((img: any) => img.image_url),
        primary_image: imagesResult.rows.find((img: any) => img.is_primary)?.image_url || product.image_url
      };
    } catch (imageError) {
      // If product_images table doesn't exist, return product with just the original image_url
      console.warn(`Could not fetch images for product ${product.id}:`, (imageError as Error).message || imageError);
      productWithImages = {
        ...product,
        images: product.image_url ? [product.image_url] : [],
        primary_image: product.image_url
      };
    }
    return NextResponse.json({
      success: true,
      data: productWithImages
    });
  } catch (error) {
    console.error('Error updating product:', error);
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, {
      status: 500
    });
  }
}
export async function DELETE(_request: NextRequest, {
  params
}: {
  params: {
    id: string;
  };
}) {
  try {
    const {
      id
    } = params;

    // Convert the id to integer for database query
    const productId = parseInt(id, 10);
    const {
      rowCount
    } = await db.query('DELETE FROM products WHERE id = $1', [productId]);
    if (rowCount === 0) {
      return NextResponse.json({
        success: false,
        error: 'Product not found'
      }, {
        status: 404
      });
    }
    return NextResponse.json({
      success: true,
      message: 'Product deleted successfully'
    });
  } catch (error) {
    console.error('Error deleting product:', error);
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, {
      status: 500
    });
  }
}